.header {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  padding: 0 0 37px;
}

@media screen and (max-width: 950px) {
  .header {
    padding: 0 0 33px;
  }
}

@media screen and (max-width: 650px) {
  .header {
    padding: 0 0 28px;
  }
}
